html {
  font-size: 62.5%;
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  font-family: "Mukta", sans-serif;
  font-size: 1.6rem; }
  body.fijar-body {
    overflow: hidden; }

p {
  font-size: 2rem;
  color: #000;
  line-height: 1.5; }

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1,
h2,
h3 {
  margin: 0; }

h1 {
  font-family: "Ephesis", cursive;
  font-size: 4rem; }

h2 {
  font-size: 3rem;
  font-family: "Merriweather", serif;
  text-align: center; }

h3 {
  font-size: 2.6rem;
  text-transform: uppercase;
  text-align: center; }

.bg-light {
  background-color: #8493F5; }

.bg-dark {
  background-color: #6975C2; }

header {
  background-color: #8493F5; }
  header .header-content {
    padding: 1rem 0; }
    @media (min-width: 768px) {
      header .header-content {
        display: flex;
        justify-content: space-between;
        align-items: center; } }
  header h1 {
    color: #fff;
    margin-bottom: 1rem;
    text-align: center; }
    @media (min-width: 768px) {
      header h1 {
        margin-bottom: 0; } }
  header.fixed {
    position: fixed;
    width: 100%;
    box-shadow: 0 0.3rem 1rem #444; }

.navbar {
  display: flex;
  gap: 2rem;
  justify-content: center; }
  .navbar a {
    color: #fff;
    font-size: 2rem; }
    .navbar a:hover {
      color: #3F4775; }

.hero {
  width: 100%;
  height: 500px;
  background: #fc5c7c91;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(-45deg, rgba(106, 130, 251, 0.63), #fc5c7c86), url(../img/hero.jpg);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(-45deg, rgba(106, 130, 251, 0.63), #fc5c7c86), url(../img/hero.jpg);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat; }
  @media (min-width: 768px) {
    .hero {
      height: 600px; } }

.hero-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%; }
  @media (min-width: 768px) {
    .hero-content {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 2rem;
      place-items: center; } }
  .hero-content img {
    width: 300px; }
  .hero-content h2 {
    text-align: center;
    font-size: 4rem;
    color: #fff; }
    @media (min-width: 480px) {
      .hero-content h2 {
        font-size: 6rem; } }

.about {
  margin: 2rem auto;
  text-align: center; }
  @media (min-width: 768px) {
    .about .text {
      padding: 0 3rem;
      margin-bottom: 3rem; } }
  .about h2 {
    font-size: 3rem; }

.servicios {
  background-color: #CFD4F7;
  text-align: center;
  padding-bottom: 2rem; }
  .servicios h2 {
    padding: 2rem 0; }
  @media (min-width: 768px) {
    .servicios-layout {
      display: flex;
      gap: 2rem; } }

.servicios-content {
  background-color: #6975C2;
  padding: 1rem;
  margin: 1rem 0;
  border-radius: 0.5rem; }
  .servicios-content i::before {
    font-size: 4rem;
    margin: 1rem; }

form {
  max-width: 900px;
  width: 95%;
  font-size: 1.8rem;
  margin: 2rem auto; }
  form .field {
    display: flex;
    align-items: center;
    margin-bottom: 1rem; }
    form .field label {
      flex: 0 0 10rem; }
    form .field input, form .field textarea, form .field select {
      flex: 1;
      border: none;
      border-bottom: 1px solid #787A87; }

h2 {
  padding: 2rem; }

.boletos {
  background-color: #8493F5;
  padding: 5px 0; }
  .boletos h3,
  .boletos p {
    text-align: center;
    color: #fff; }
  .boletos .boletos-content {
    margin-top: 5px/2;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5px; }
  .boletos .plan p {
    font-size: 3rem;
    margin-bottom: 2rem; }

.pase {
  width: 100%;
  border-spacing: 0; }
  .pase thead {
    background-color: #CFD4F7; }
    .pase thead th {
      padding: 5px/4;
      color: #fff;
      text-transform: uppercase;
      font-weight: 300;
      font-size: 2rem; }
  .pase tbody {
    background-color: #fff; }
    .pase tbody td {
      padding: 5px/4;
      border-bottom: 1px solid #e1e1e1; }
    .pase tbody tr:last-of-type td {
      border: none; }
  .pase tfoot {
    background-color: #3F4775; }
    .pase tfoot td {
      padding: 5px/4;
      color: #fff;
      font-size: 3rem;
      text-align: center; }

footer {
  background-color: #8493F5;
  padding: 1rem;
  text-align: center; }
  footer p {
    color: #fff;
    margin: 0; }
    footer p:last-child {
      font-size: 1.8rem; }
    footer p a {
      color: #fff;
      font-size: 2rem; }
      footer p a:hover {
        color: #3F4775; }
