.boletos {
  background-color: $primary-light-color;
  padding: 5px 0;
  h3,
  p {
    text-align: center;
    color: $light-color;
  }
  .boletos-content {
    margin-top: 5px/2;
    @include grid(2, 5px);
  }
  .plan {
    p {
      font-size: 3rem;
      margin-bottom: 2rem;
    }
  }
}

.pase {
  width: 100%;
  border-spacing: 0;
  thead {
    background-color: $secondary-light-color;
    th {
      padding: 5px/4;
      color: $light-color;
      text-transform: uppercase;
      font-weight: $thin;
      font-size: 2rem;
    }
  }
  tbody {
    background-color: $light-color;
    td {
      padding: 5px/4;
      border-bottom: 1px solid #e1e1e1;
    }
    tr {
      &:last-of-type {
        td {
          border: none;
        }
      }
    }
  }
  tfoot {
    background-color: $primary-dark-color;
    td {
      padding: 5px/4;
      color: $light-color;
      font-size: 3rem;
      text-align: center;
    }
  }
}
